    import {mapState} from 'vuex';

  	module.exports = {
  		data: function () {
  			return {
          isShow: false
  			}
  		},
	    mounted: function () {
				this.$nextTick(function () {

				})
	    },
	    methods: {
          hideAlert: function () {
            this.$store.commit('isShowAlert', false);
          }
	    },
      computed: mapState({
        isShow: function (state) {
          return state.isShow;
        },
      }),
  	}



